.header {

    & > .container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__nav {
        
        ul {
            display: flex;
            list-style: none;
            white-space: nowrap;
          }
        & > ul > li {
            margin-right: 40px;
        }

        & ul a {
         color: currentColor;
         text-decoration: none;
         font-size: 20px;
         margin-right: 80px;
         font-weight: 700;

        }
        & ul a:hover {
         color: blue;
        }
    }
}