@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap');

body {
    margin: 0 auto;
    color:#fff;
    font-family: 'Roboto', sans-serif;

}
.bg {
    position: fixed;
    z-index: -1;
}
.container {
    max-width: 1520px;
    padding: 10px 10px;
    margin: 0 auto;
}
.parallax {
   
    position: relative;
   .par1 {
        max-width:50%;
        position: absolute;
        top: 90px;
        left: 100px;
    }
    .par2 {
        max-width:50%;
        position: absolute;
        top: 180px;
        right: 250px;

    }
    .par3 {
        max-width:80%;
        
        position: absolute;
        top: 350px;
        right: 0;

    }
    &__img {
        max-width: 50%;
        border-radius: 50%;
        
    }
}
.wow {
    visibility: hidden;
}














